.partners-page{
  overflow: hidden;
}

.how-work{
  margin-top: 7.5em;
  &__steps{
    margin-top: 2em;
    padding-top: 2.25em;
  }
  &__nav{
    margin-top: 2em;
  }
}

.step-card{
  padding: 3.25em 1.5em 1.5em;
  border-radius: 1.75em;
  position: relative;
  text-align: center;
  border: $border;
  &__icon{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
  }
  &__title{
    @media screen and (min-width: 993px){
      min-height: 2.8em;
    }
  }
  &__desc{
    margin-top: 1em;
  }
}

.btn-group{
  display: flex;
  &--center{
    justify-content: center;
  }
}

.partners-info{
  margin-top: 7.5em;
  display: grid;
  gap: 3em;
  grid-template-columns: repeat(2,calc(50% - 1.5em));
  &__desc{
    margin-top: 1.5em;
  }
  .info-block{
    margin-top: 2em;
  }
}

.awards{
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: calc(100% + 2em);
}

.award-item{
  width: calc(50% - 2em);
  margin: 2em 2em 0 0;
  @include grid100gap(1em);
}


@media screen and (max-width: 992px){
  .how-work{
    &__steps{
      grid-template-columns: 100%;
      gap: 2.75em;
    }
  }

  .partners-info{
    grid-template-columns: 100%;
    text-align: center;
  }

  .awards{
    justify-content: center;
  }
}


@media screen and (max-width: 576px){
  .award-item{
    width: 100%;
  }
}
