.auth{
  padding: 4.5em 0 3em;
  &__wrap{
    margin: 3em auto 0;
    width: 27em;
    max-width: 100%;
    //box-shadow: $shadow;
    position: relative;
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__links{
    margin-top: 2em;
    display: flex;
    justify-content: space-between;
    &--center{
      justify-content: center;
    }
  }
  &__link{
    color: $primary;
    text-decoration: underline;
  }
  &__links2{
    margin-top: 1.5em;
  }
  &--activate{
    .m-captcha{
      margin-top: 2em;
    }
  }
}

.m-captcha{
  margin-top: 1em;
  display: flex;
  justify-content: center;
  &__img{
    margin: 0 auto;
    max-width: 19.8125em;
    display: block;
    width: 100%;
  }
}

.account{
  .auth__wrap{
    @media screen and (min-width: 577px){
      width: 100%;
    }
  }
  &__hint{
    margin-top: 2em;
    text-align: center;
    color: $gray;
    a{
      color: $primary;
      text-decoration: underline;
    }
  }
}

.form-fields{
  position: relative;
  &__error{
    position: absolute;
    top: 100%;
    margin-top: 1em;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 1em;
    max-width: 24.5em;
    width: 100%;
    background-color: $bittersweet;
    color: $eerieBlack;
    display: flex;
    align-items: center;
    border-radius: .75em;
    min-height: 1.5em;
  }
}
