.logo{
  display: block;
  width: 16.25em;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}

.footer{
  padding: 3.5em 0 1.5em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .socials{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__right{
    display: flex;
  }
  &__col{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__c{
    margin-top: auto;
    color: $gray;
  }
  .lang{
    margin-left: 10.75em;
    .m-dropdown__hidden{
      top: unset;
      bottom: 100%;
      margin: 0 0 .5em;
      box-shadow: 0 -1em -.5em 0 rgba(0,0,0,.4);
    }
  }
}

.footer-nav{
  display: flex;
  &__group{
    @include lastM(0 3em 0 0);
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    margin-top: 1em;
    font-size: 1em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 5.875em;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .footer{
    &__inner{
      display: block;
    }
    &__left{
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
    }
    .lang{
      margin: 0;
    }
    .socials{
      padding: 0;
      margin: 0;
    }
    &__right{
      margin-top: 3em;
      justify-content: space-between;
    }
    &__bottom{
      margin-top: 2.25em;
      display: block;
    }
  }

  .footer-nav{
    &__group{
      margin-right: 0 !important;
      &:not(:first-child){
        margin-left: 3em !important;
      }
    }
  }

  .footer-partners{
    flex-wrap: wrap;
    width: calc(100% + .75em);
    justify-content: center;
    &__item{
      margin: .75em .75em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__right{
      display: block;
      position: relative;
    }
    .lang{
      margin: 0;
      position: absolute;
      right: 0;
      bottom: 0;
    }
    &__inner{
      position: relative;
      padding-bottom: 4.5em;
    }
    &__c{
      position: absolute;
      top: 100%;
      margin-top: 3em;
      left: 0;
      width: 100%;
      text-align: center;
    }
  }

  .footer-nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    &__group{
      @include lastM(0);
      &:not(:first-child){
        margin-left: 0 !important;
      }
      &--mt-48{
        margin-top: -3em !important;
      }
    }
  }
}
