.tabs{
  display: flex;
  flex-wrap: wrap;
}

.tab{
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 900;
  transition: all .3s;
  color: $gray;
  margin: 1em 1em 0 0;
  //background-color: $anti-flash-white;
  border-top: 2px solid transparent;
  border-bottom: 2px dashed $gray;
  &._type2{
    border: 2px solid $jet;
    height: 3.5em;
    padding: 0 1.5em;
    border-radius: 1.75em;
    color: #fff;
    margin-right: .5em;
    &._active,
    &:hover{
      color: $eerieBlack;
      border-color: $primary;
      background-color: $primary;
    }
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &:hover{
    cursor: pointer;
  }
  &._active,
  &:hover{
    color: $primary;
    border-color: transparent;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
