.m-section{
  padding: 3.75em 0;
}

.sell{
  &__top{
    text-align: center;
  }
  &__bottom{
    margin-top: 3em;
  }
}

.exchange{
  position: relative;
  &._blocked{
    .exchange-blocked{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .loader{
    &__wrap{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      z-index: 10;
      @include center;
      background-color: rgba(#fff,.5);
    }
  }
  &._show-loader{
    .loader__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__reverse{
    padding: 0;
    height: 3.5em;
    @include center;
    border: 2px solid $jet;
    border-radius: 50%;
    img{
      width: 1.5em;
    }
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    justify-content: center;
  }
  &__hint{
    margin: 2em auto 0;
    width: 33em;
    max-width: 100%;
    text-align: center;
    color: $gray;
    a{
      color: $primary;
    }
    a:not(:hover){
      text-decoration: underline;
    }
  }
}

.exchange-info{
  display: flex;
  justify-content: space-between;
  padding: 1em 1.5em;
  border: $border;
  border-radius: 2.5em;
  &__rate{
    font-weight: 500;
  }
  &__rate2{
    margin-left: 1em;
    padding-left: 1em;
    margin-right: auto;
    //border-left: $border;
    min-height: 2.2em;
    display: flex;
    align-items: center;
  }
  &__hint{
    margin-left: 1em;
    padding-left: 1em;
    //border-left: $border;
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin-left: .5em;
    }
  }
  &__hint2{
    border: none;
    padding: 0;
    margin: 0 1em 0 0;
  }
}

.course-type{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 .5em 0 0);
    input:hover,
    input:checked{
      &~label{
        //background-color: $onyx;
        color: $primary;
        border-color: transparent;
      }
    }
    input:disabled{
      pointer-events: none;
      &~label{
        background-color: transparent;
        opacity: .2;
      }
    }
    label{
      transition: all .3s;
      @include center;
      //background-color: $anti-flash-white;
      font-weight: 900;
      border-top: 2px solid transparent;
      border-bottom: 2px dashed $gray;
      color: $gray;
      span{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }
}

.course-info{
  display: flex;
  align-items: flex-start;
  @include lastM(0 0 1em);
  svg{
    width: 1.5em;
    margin-right: 1em;
  }
}

.m-tooltip{
  position: relative;
  &:hover{
    cursor: pointer;
    .m-tooltip__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__wrap{
    z-index: 10;
    position: absolute;
    left: 100%;
    bottom: 100%;
    transform: translate(-1.5em,1.5em);
    padding: 0 0 1.5em 1.5em;
    pointer-events: none;
    opacity: 0;
    transition: all .3s;
  }
  &__inner{
    width: 50em;
    padding: 1.5em;
    background-color: $eerieBlack;
    border: $border;
    box-shadow: $shadow2;
    border-radius: 1.75em 1.75em 1.75em 0;
    color: #fff;
  }
}

.exchange-blocked{
  opacity: 0;
  pointer-events: none;
  border-radius: 1.75em;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  @include center;
  flex-direction: column;
  padding: 1.5em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background-color: rgba(#000,.4);
    backdrop-filter: blur(16px);
    z-index: -1;
  }
  &__img{
    margin-bottom: 1.5em;
    height: 8em;
    filter: $drop-shadow2;
  }
}

._flex-center,
._flex{
  display: flex;
  align-items: center;
}

._flex-center{
  justify-content: center;
}

.exchange-items{
  display: grid;
  grid-template-columns: calc(50% - 2.75em) 3.5em calc(50% - 2.75em);
  gap: 1em;
  align-items: center;
  margin-top: 2em;
}

.exchange-item{
  position: relative;
  z-index: 5;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .125em));
  gap: .5em;
  .fg{
    position: relative;
    z-index: 2;
    input{
      border-radius: 2.5em 0 0 2.5em;
    }
  }
  &._open{
    z-index: 6;
    .currency-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .currency-select{
      border-color: #fff;
      outline: 1px solid #fff !important;
      &__arrow{
        transform: rotate(-180deg) translate(-50%,50%);
      }
    }
  }
}

.currency-select{
  z-index: 2;
  width: 100%;
  height: 100%;
  border: $border;
  position: relative;
  border-radius: 0 2.5em 2.5em 0;
  padding: .5em 1.5em .5em 4em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  &__wrap{
    display: flex;
    align-items: center;
    color: #fff;
    margin-top: .25em;
  }
  &__icon{
    width: 1.5em;
    margin-right: .5em;
    position: absolute;
    top: 50%;
    left: 1.5em;
    transform: translateY(-50%);
  }
  &__arrow{
    margin-left: auto;
    width: 1.5em;
    transition: all .3s;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1.5em;
  }
}

.currency-dropdown,
.city-dropdown{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  border-radius: 1em;
  border: 2px solid #fff;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: .5em;
  width: 100%;
  padding: .5em 1.5em 0;
  box-shadow: 0 .5em 1.5em rgba(49, 54, 56, 0.1);
  background-color: $jet;
  &__inner{
    margin-top: 1.5em;
    max-height: 15.5em;
    overflow-y: auto;
    @include scroll;
    width: calc(100% + 1em);
  }
  &__items{
    @include grid100gap(1em);
    padding-bottom: 1.5em;
  }
}

.currency-dropdown{
  .tabs{
    max-width: 36em;
    width: 100%;
  }
}

.currency-item{
  display: grid;
  align-items: center;
  grid-template-columns: 1.5em calc(50% - 1.25em) calc(50% - 1.25em);
  gap: .5em;
  //color: $onyx;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: $primary;
  }
  &--disabled{
    //color: $timberwolf;
    pointer-events: none;
  }
  &__icon{
    width: 100%;
  }
  &__desc{
    color: $gray;
  }
}

.exchange-items2{
  margin-top: 1em;
}


@media screen and (max-width: 992px){
  .exchange{
    &__reverse{
      width: 3.5em;
      margin: 0 auto;
    }
  }

  .exchange-info{
    flex-direction: column;
    align-items: center;
    &--type2{
      flex-direction: row;
      flex-wrap: wrap;
      .exchange-info__left{
        width: 100%;
        justify-content: space-between;
        padding-bottom: 1em;
        margin-bottom: 1em;
        border-bottom: $border;
      }
      .exchange-info__right{
        margin-top: 0;
      }
    }
    &__rate2{
      padding: 0;
      margin-left: 0;
      border: none;
      min-height: unset;
    }
    &__right{
      margin-top: 1em;
    }
    .m-tooltip{
      text-align: right;
    }
    &__hint2{
      .m-tooltip__wrap{
        @media screen and (min-width: 577px){
          left: 100%;
          right: unset;
        }
      }
    }
  }

  .m-tooltip{
    &__wrap{
      left: unset;
      right: 0;
      padding-left: 0;
      transform: translate(0,1.5em);
      text-align: left;
    }
    &__inner{
      width: 21.5em;
      border-radius: 1em 1em .25em 1em;
    }
  }

  .exchange-items{
    grid-template-columns: 100%;
  }

  .exchange-blocked{
    .info-block__desc{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .exchange{
    border-radius: 0;
  }

  .exchange-info{
    text-align: center;
    &__left{
      display: block;
      width: 100%;
    }
    &__hint{
      border-left: none;
      padding: 1em 0 0;
      margin: 1em 0 0;
      border-top: $border;
    }
    .m-tooltip{
      width: 100%;
      justify-content: center;
      span{
        max-width: 8.5em;
      }
    }
    &--type2{
      display: block;
      .exchange-info__left{
        flex-direction: column;
        align-items: center;
        display: flex;
      }
      .exchange-info__right{
        justify-content: center;
        margin-top: 1em;
      }
      .m-tooltip{
        text-align: unset;
        span{
          max-width: unset;
        }
      }
    }
    &__hint2{
      padding-top: 0;
      border: none;
      margin: 0 0 1em;
    }
    &__rate2{
      justify-content: center;
    }
  }

  .m-tooltip{
    &__wrap{
      right: 50%;
      transform: translate(50%,2.75em);
    }
    &__inner{
      width: 20.5em;
    }
  }

  .exchange-item{
    grid-template-columns: 100%;
    gap: .25em;
    .fg {
      input{
        border-radius: 2.5em 2.5em 0 0;
      }
    }
  }

  .currency-select{
    border-radius: 0 0 2.5em 2.5em;
    height: 5em;
  }

  .exchange-items2{
    padding: 0;
  }

  .exchange-blocked{
    padding: 1em;
  }
}
