.deal-success{
  margin-top: 3em;
  @include grid100gap(2em);
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
    word-break: break-word;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.deal-review{
  margin-top: 1em;
  overflow: hidden;
  position: relative;
  padding: 3em;
  border-radius: 1.75em;
  &__content{
    max-width: 21.5em;
    position: relative;
    z-index: 3;
  }
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    border-radius: inherit;
    img{
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    &:after{
      background: rgba(#000,.8);
      border: $border;
    }
  }
}

.deal-stopped{
  margin-top: 3em;
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}

.card-info{
  position: relative;
  padding-top: 3.25em;
  .info-block__img{
    position: absolute;
    margin: 0;
    left: 50%;
    transform: translateX(-50%);
    top: -6.25em;
  }
}

.deal-verification{
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__card{
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: $border;
    padding: 1em 1.5em;
    border-radius: 1.75em;
    position: relative;
    &-item{
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 1.5em;
        margin-right: 1em;
      }
    }
    &-btn{
      position: absolute;
      padding: 0 1.5em;
      right: .25em;
      top: .25em;
      height: calc(100% - .5em);
    }
  }
  &__hint{
    margin-top: 1.5em;
    display: flex;
    img{
      width: 1.5em;
      margin-right: 1em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .deal-success{
    &__item{
      flex-direction: column;
      text-align: center;
      align-items: center;
    }
  }

  .deal-review{
    padding: 1.5em;
  }

  .deal-verification{
    &__card{
      @include grid100gap(1em);
      text-align: center;
      &._type2{
        .deal-verification__card-item{
          justify-content: unset;
        }
      }
    }
  }
}
