.about-page{
  &__items{
    margin-top: 2em;
  }
  &__desc{
    margin-top: 2em;
  }
}

.services{
  margin-top: 7.5em;
  &__items{
    margin-top: 3.75em;
    gap: 2.75em;
  }
}

.geography{
  margin-top: 7.5em;
  &__desc{
    margin-top: 2em;
    text-align: center;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1em;
    width: calc(100% + 1em);
    justify-content: center;
  }
}

.geography-item{
  margin: 1em 1em 0 0;
  border-radius: 1em;
  padding: 1em 1.5em;
  display: flex;
  align-items: center;
  border: $border;
  &__flag{
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    margin-right: 1em;
    flex-shrink: 0;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .about-page{
    &__items{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }
}
