:root {
  --vh: 1vh;
}

$font1:'Lato',sans-serif;
$font2:'Jost',sans-serif;
$black: #000;
$primary: #f8c630;
$secondary: blue;
$eerieBlack: #201f1d;
$jet: #292826;
$gray: #797977;
$white: #fff;
$bittersweet: #fe5f55;
$green: #4cb944;

$border: 1px solid $jet;
$shadow2: 0 1em .5em 0 rgba(0, 0, 0, 0.4);
$drop-shadow2: drop-shadow(0 1em .5em rgba(0, 0, 0, 0.4));

$vh: calc(100 * var(--vh));
