.deal{
  &__inner{
    max-width: 100%;
    width: 41em;
    margin: 0 auto;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 28em) 27em;
    gap: 1em;
  }
}

.deal-verification,
.card-info,
.deal-stopped,
.deal-success,
.deal-sending,
.deal-processing,
.deal-info,
.deal-pay{
  padding: 1.5em;
  border: $border;
  border-radius: 1.75em;
}

.deal-info{
  &__top{
    @include grid100gap(1em);
  }
  &__item{
    color: $gray;
    span{
      color: #fff;
      font-weight: 900;
    }
  }
  &__attention{
    margin-top: 2em;
  }
  .text{
    ul{
      gap: 0;
    }
  }
  &__bottom{
    margin-top: 2em;
    display: flex;
  }
  &__cancel{
    font-size: 1em;
    line-height: 150%;
    color: $gray;
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
}

.deal-bread{
  display: flex;
  align-items: center;
  margin-bottom: 1em;
  font-weight: 900;
  color: $gray;
  white-space: nowrap;
  &__arrow{
    width: 1.5em;
    flex-shrink: 0;
    margin: 0 1em;
  }
  &__item{
    display: flex;
    align-items: center;
    span{
      border-top: 2px dashed transparent;
      border-bottom: 2px dashed transparent;
    }
    &._active{
      color: $primary;
    }
    &._done{
      &:before{
        content: '';
        flex-shrink: 0;
        width: 1.5em;
        height: 1.5em;
        margin-right: .5em;
        background-image: url(../img/success.svg);
        @include bgImgSize(100%);
      }
      span{
        border-bottom-color: $gray;
      }
      &._active{
        span{
          border-color: transparent;
        }
      }
    }
  }
}

.deal-pay{
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: calc(100% - 10.5em) 9.5em;
    gap: 1em;
    align-items: flex-start;
    &--full{
      grid-template-columns: 100%;
    }
    .fg{
      label{
        color: $gray;
      }
      input{
        padding-right: 11em;
      }
      .copy-btn2{
        position: absolute;
        top: 50%;
        right: .25em;
        transform: translateY(-50%);
        height: calc(100% - .5em);
      }
    }
  }
  &__code{
    padding: .5em;
    border-radius: 1.75em;
    border: $border;
    //background-color: $anti-flash-white;
    text-align: center;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hint{
    margin-top: 2em;
    text-align: center;
  }
  &__bottom{
    margin-top: 2em;
    text-align: center;
    @include grid100gap(2em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    &._type2{
      justify-content: unset;
    }
  }
}

.deal-sending{
  &._active{
    .deal-sending__item-desc{
      color: $primary;
    }
  }
  &__info{
    @include grid100gap(1em);
  }
  &__item{
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    &-desc{
      text-align: right;
      font-weight: 900;
    }
  }
}

.copy-btn2{
  text-transform: unset;
  border-radius: 2.25em;
  img{
    width: 1.5em;
    margin-right: .5em;
  }
}

.deal-total{
  margin-top: 2em;
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
    word-break: break-word;
  }
}

.m-spoiler{
  &__show{
    cursor: pointer;
    &._disabled{
      pointer-events: none;
    }
  }
  &__hidden{
    display: none;
  }
}

.verification{
  .info-block__desc{
    width: 100%;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__status{
    position: relative;
    margin-top: 2em;
    border-radius: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1em 1.5em;
    //background-color: $white-smoke;
    border: $border;
  }
  &__hint{
    margin-top: 1.5em;
    @include center;
    text-align: center;
  }
  &__img{
    width: 1.5em;
    margin-right: .5em;
  }
  &__info{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  &__btn{
    border-radius: .75em;
    text-transform: unset;
    position: absolute;
    right: .25em;
    top: 50%;
    transform: translateY(-50%);
    height: calc(100% - .5em);
  }
}



@media screen and (max-width: 992px){
  .deal{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .deal-info{
    @media screen and (min-width: 577px){
      &__top{
        br{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .copy-btn2{
    width: 4.5em;
    img{
      margin: 0;
    }
    span{
      display: none;
    }
  }

  .deal-pay{
    &__grid{
      grid-template-columns: 100%;
    }
    &__code{
      width: 11em;
      margin: 0 auto;
    }
    &__fields{
      .fg{
        input{
          padding-right: 4.5em;
        }
      }
    }
  }

  .deal-bread{
    @include fullScreenMob;
    @include nullScroll;
    overflow-x: auto;
    padding: 0 1em;
  }

  .deal-total{
    &__item{
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  .verification{
    &__status{
      flex-direction: column;
      align-items: center;
      text-align: center;
      &._not-center{
        flex-direction: row;
      }
    }
    &__card{
      margin-top: .94em;
    }
  }
}
