@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.messages{
  @include grid100gap(1em);
}

.alert-container{
  //pointer-events: none;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 1em;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 5;
  .info-alert{
    position: static;
    margin: 0 0 1em;
    max-width: 100%;
  }
}

.info-alert{
  max-width: 100%;
  margin: 1.25em auto -1.25em;
  display: flex;
  align-items: center;
  padding: 1em;
  border: 1px solid $bittersweet;
  border-radius: 2.5em;
  box-shadow: $shadow2;
  background-color: $eerieBlack;
  color: #fff;
  &__wrap{
    display: flex;
    justify-content: center;
  }
  &__icon{
    flex-shrink: 0;
    @include center;
    width: 2.5em;
    height: 2.5em;
    background-color: $bittersweet;
    border-radius: 50%;
    margin-right: 1em;
    img{
      width: 1.5em;
    }
  }
  &--green{
    border-color: $green;
    .info-alert__icon{
      background-color: $green;
    }
  }
  &--yellow{
    border-color: $primary;
    .info-alert__icon{
      background-color: $primary;
    }
  }
}

.loader {
  color: $primary;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  animation: load4 1.3s infinite linear;
  transform: translateZ(0);
}
@keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    border-radius: 50%;
    width: 3.5em;
    @include lastM(0 1em 0 0);
    text-transform: unset;
    img{
      width: 1.5em;
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    pointer-events: none;
    border-radius: inherit;
  }
}

.info-block{
  text-align: center;
  &__img{
    margin-bottom: 1.5em;
    height: 8em;
    filter: $drop-shadow2;
    display: flex;
    justify-content: center;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
  &__icon{
    margin: 0 auto 1.5em;
    flex-shrink: 0;
  }
  &__links{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
  &__desc{
    text-align: center;
    margin: 1.5em auto 0;
    width: 55em;
    max-width: 100%;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
  }
  &._type2{
    .info-block__desc{
      margin-top: 1em;
    }
  }
}
