.h1,
.text32{
  font-size: 2em;
  line-height: 112.5%;
}

.text h2,
.h2,
.text28{
  font-size: 1.75em;
  line-height: 114%;
}

.text h2,
.h1,
.h2,
.h3,
.h4{
  text-transform: uppercase;
  font-weight: 900;
}

.h3,
.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.h4,
.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 150%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-black-text{
  font-weight: 900;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.caps-text{
  text-transform: uppercase;
}

.gray-text{
  color: $gray;
}

.primary-text{
  color: $primary;
}

.red-text{
  color: $bittersweet;
}

.text-center{
  text-align: center;
}

.text-underline{
  text-decoration: underline;
}


@media screen and (max-width: 992px){
  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px){
  .text16-mob{
    font-size: 1em;
    line-height: 150%;
  }
}
