.warning-info{
  padding: 0 1.5em 1.5em;
  border-radius: 1.75em;
  border: $border;
  width: 55em;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__text{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__icon{
    margin-top: -1.75em;
  }
}

.m-icon{
  width: 3.5em;
  height: 3.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $jet;
  border-radius: 50%;
  flex-shrink: 0;
  box-shadow: $shadow2;
  img{
    width: 1.5em;
  }
  &._type2{
    box-shadow: none;
    border: $border;
    background-color: transparent;
  }
}

.history{
  &__grid{
    margin-top: 3em;
  }
}

.history-card{
  padding: 1.5em;
  text-align: center;
  border: $border;
  border-radius: 1.75em;
  &__wrap{
    display: grid;
    grid-template-columns: calc(50% - 1.75em) 1.5em calc(50% - 1.75em);
    gap: 1em;
    margin-top: 1em;
    align-items: center;
  }
  &__arrows{
    width: 100%;
    display: block;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon{
    height: 1.5em;
    margin-bottom: .5em;
  }
  &__desc{
    @include lineTruncate;
  }
}

.mt48{
  margin-top: 3em;
}

.mt32{
  margin-top: 2em;
}

.mt24{
  margin-top: 1.5em;
}

.make-exchange{

}

.exchange-step{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__num{
    margin-bottom: 1em;
  }
  &__img{
    margin-top: 1.5em;
    position: relative;
    width: 20.75em;
    max-width: 100%;
    height: 9em;
    @include center;
    img{
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      max-width: 105%;
    }
    &._type2{
      img{
        max-width: 11.5em;
        position: static;
        box-shadow: $shadow2;
        transform: unset;
        border-radius: 1.75em;
      }
    }
  }
}

.service-card{
  padding-top: 6.5em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 8em;
    filter: $drop-shadow2;
  }
  &__content{
    padding: 3em 1.5em 1.5em;
    border: $border;
    border-radius: 1.75em;
    @include grid100gap(1em);
    text-align: center;
  }
}

.about-info{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.5em));
  gap: 1.5em 3em;
}

@keyframes slides {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

.partners{
  overflow: hidden;
  &__line{
    display: flex;
    margin-top: 3em;
  }
  &__items{
    flex-shrink: 0;
    display: flex;
    animation: 18s linear infinite slides;
  }
  &__item{
    height: 3em;
    flex-shrink: 0;
    margin-right: 3em;
    &:not(:hover){
      filter: grayscale(100%);
    }
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
}

.main{
  padding: 1.5em 1.5em 3.75em;
  overflow: hidden;
}

.main-slider{
  width: 1872px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
  border-radius: 2.5em;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 2.5em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      border: $border;
    }
    &-pagination{
      position: static;
      width: auto;
      z-index: 1;
      margin: 0 .5em;
      &-bullet{
        flex-grow: 1;
        width: .5em;
        height: .5em;
        margin: 0 .5em !important;
        opacity: 1;
        background-color: #fff;
        position: relative;
        &-active{
          background-color: $primary;
        }
      }
    }
    &-button{
      padding: 0;
      width: 3.5em;
      height: 3.5em;
      position: static;
      margin: 0;
      border-radius: 50%;
      background-color: $eerieBlack;
      svg{
        width: 1.5em;
      }
      &-prev{
        left: 1.5em;
      }
      &-next{
        right: 1.5em;
      }
    }
  }
  &__nav{
    position: absolute;
    left: 0;
    bottom: 1.5em;
    width: 100%;
    .container{
      display: flex;
      align-items: center;
    }
  }
}

.main-banner{
  padding: 8.25em 0;
  background-size: cover;
  background-position: center;
  border-radius: 2.5em;
  color: #fff;
  position: relative;
  overflow: hidden;
  min-height: 30.75em;
  &__img{
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    max-width: 41em;
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .make-exchange{
    &__grid{
      row-gap: 1.5em;
    }
  }

  .exchange-step{
    @media screen and (min-width: 577px){
      flex-direction: row;
      text-align: left;
      &__num{
        margin: 0 1em 0 0;
      }
      &__img{
        flex-shrink: 0;
        margin: 0 0 0 1em;
      }
    }
  }

  .about-info{
    grid-template-columns: 100%;
    .h2{
      text-align: center;
    }
  }

  .main{
    padding-top: 0;
  }

  .main-banner{
    padding: 3em 0;
    min-height: 33em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .make-exchange{
    &__grid{
      row-gap: 1em;
    }
  }

  .exchange-step{
    &__img{
      img{
        width: 100%;
      }
    }
  }

  .main{
    padding-left: 1em;
    padding-right: 1em;
  }

  .main-banner{
    padding: 1.5em 0;
    min-height: 41em;
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }
}
