.page-block{
  padding: 7.5em 0 3.75em;
}

.reviews{
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: calc(100% + 1em);
    margin-top: 2em;
  }
}

.review-card{
  @include center;
  //width: calc(100% / 6 - 1em);
  margin: 1em 1em 0 0;
  padding: .5em 1em;
  border-radius: 2.5em;
  border: $border;
  height: 5em;
  &:not(:hover){
    filter: grayscale(100%);
  }
  @media screen and (min-width: 577px){
    &.review-card--medium{
      //width: 17.0625em;
    }
    &.review-card--big{
      //width: 23.75em;
    }
  }
  img{
    max-width: 100%;
    max-height: 3em;
  }
}


@media screen and (max-width: 992px){
  .page-block{
    padding-top: 6em;
  }
}


@media screen and (max-width: 576px){
  .review-card{
    padding-left: 1.5em;
    padding-right: 1.5em;
    height: 3.5em;
    img{
      max-height: 1.5em;
    }
  }
}
