.city-field{
  position: relative;
  .fg{
    cursor: pointer;
    z-index: 3;
    input{
      pointer-events: none;
      padding-left: 3.55em;
    }
    label{
      left: 3.55em;
    }
  }
  &__flag,
  &__arrow{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }
  &__flag{
    left: 1.5em;
    width: 1.625em;
    border: $border;
    border-radius: 50%;
  }
  &__arrow{
    width: 1.5em;
    right: 1.5em;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
  &._open{
    z-index: 5;
    .city-field__arrow img{
      transform: rotate(-180deg);
    }
    .city-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.city-dropdown{
  padding-top: 1.5em;
  //opacity: 1;
  //pointer-events: unset;
  &__inner{
    max-height: 20em;
    margin: 0;
  }
}

.city-option{
  display: grid;
  grid-template-columns: 1.5em calc(50% - 1.25em) calc(50% - 1.25em);
  align-items: center;
  gap: .5em;
  cursor: pointer;
  &__col{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.5em;
    height: 1.5em;
    object-fit: cover;
    border-radius: 50%;
    border: $border;
  }
}
