@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    font-weight: 900;
    height: 3.5em;
    border-radius: 1.75em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 150%;
    }
    &--big{
        height: 5em;
    }
    &--small{
        height: 2.5em;
        padding: 0 1.25em;
        border-radius: .5em;
        text-transform: unset;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $eerieBlack;
    &:hover{
        background-color: darken($primary,10%);
        color: $eerieBlack;
    }
    &:disabled{
        opacity: .2;
        pointer-events: none;
    }
}

